import React from 'react'
import {
	Switch,
	useLocation,
	Route
} from "react-router-dom"
import {
	AnimatePresence
} from "framer-motion"

import Home from "./pages/Home"
import About from "./pages/About"
import Projects from "./pages/Articles/Projects"
import Writings from "./pages/Articles/Writings"

const App = () => {
	const location = useLocation()

	return (
		<AnimatePresence initial={false} exitBeforeEnter>
			<Switch location={location} key={location.pathname}>
				<Route path="/website/" exact component={Home} />
				<Route path="/website/about" exact component={About} />
				<Route path="/website/projects" exact component={Projects} />
				<Route path="/website/articles" exact component={Writings} />
			</Switch>
		</AnimatePresence>
	)
}

export default App
