import styled from "styled-components"
import { motion } from "framer-motion"

const AboutContainerAnimation = {
	in: {
		x: 0,
		transition: {
			duration: 1
		}
	},
	out: {
		x: '-100vw',
		transition: {
			duration: 1
		}
	}
}

const AboutContainerTextAnimation = {
	in: {
		y: 0,
		transition: {
			delay: 2,
			duration: 2
		}
	},
	out: {
		y: '100vh',
		transition: {
			delay: 2,
			duration: 2
		}
	}
}

const AboutContainerContentAnimation = {
	in: {
		y: 0,
		transition: {
			delay: 4,
			duration: 2
		}
	},
	out: {
		y: '100vh',
		transition: {
			delay: 4,
			duration: 2
		}
	}
}

const MediaContainerContentAnimation = {
	in: {
		x: 0,
		transition: {
			delay: 4,
			duration: 2
		}
	},
	out: {
		x: '-100vw',
		transition: {
			delay: 4,
			duration: 2
		}
	}
}

export const AboutContainer = styled(motion.div).attrs(() => ({
	initial: "out",
	animate: "in",
	exit: "out",
	variants: AboutContainerAnimation,
}))`
	width:100%;
	height:100%;
	background-color:black;
	display:grid;
	grid-template-columns: 20% 60% 20%;
	grid-template-rows: 100%;
	overflow:auto;

	@media screen and (max-width:800px) {
		grid-template-columns: 100%;
		grid-template-rows: 40% auto 30%;
	}
`

export const AboutTextContainer = styled(motion.div).attrs(() => ({
	initial: "out",
	animate: "in",
	variants: AboutContainerTextAnimation,
}))`
	grid-column: 2 / 3;
	grid-row: 1 / 2;
	width:90%;
	padding-left:5%;
	padding-right:5%;
	height:100%;
	display:grid;
	grid-template-rows: 20% 80%;

	@media screen and (max-width:800px) {
		grid-column: 1 / 2;
		grid-row: 2 / 3;
	}
`

export const AboutTextContainerHeader = styled.div`
	grid-row: 1 / 2;
	color:white;
	display:flex;
	justify-content: center;
	align-items:flex-end;
	font-size: 4em;
	font-family: IBM Plex Sans Bold Italic;
`

export const AboutTextContainerContent = styled(motion.div).attrs(() => ({
	initial:"out",
	animate: "in",
	variants: AboutContainerContentAnimation
}))`
	padding-top:20px;
	grid-row: 2 / 3;
	color:white;
	font-size: 1.5em;
	font-family: IBM Plex Sans Thin;
`

export const AboutMediaContainer = styled(motion.div).attrs(() => ({
	initial:"out",
	animate: "in",
	variants: MediaContainerContentAnimation
}))`
	grid-column: 1 / 2;
	grid-row: 1 / 2;

	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	align-items:center;

	padding-top:10px;
	border-radius: 20px;
	margin:20px;
	border: 1px white solid;
`

export const ProfileImage = styled.img`
	width:200px;
	height:200px;
	border-radius:200px;
	
	@media screen and (max-width:1400px) {
		width:150px;
		height:150px;
		border-radius:150px;
	}
	@media screen and (max-width:1050px) {
		width:100px;
		height:100px;
		border-radius:150px;
	}
`

export const AboutBackContainer = styled.div`
	grid-column: 3 / 4;
	display:flex;
	justify-content:center;
	align-items:center;

	@media screen and (max-width: 800px) {
		grid-column: 1 / 2;
		grid-row: 3 / 4;
	}
`

export const MediaList = styled.ul`
	list-style-type: '@';
`

export const MediaItem = styled.li`
	color:white;
	font-size:30px;
	font-family: IBM Plex Sans Regular;

	@media screen and (max-width: 1000px) {
		font-size:18px;
	}
	
`

export const MediaLink = styled.a`
	text-decoration:none;
	color:white;
`

