import { createGlobalStyle } from "styled-components"

import IBMPlexSans_Regular from "./IBMPlexSans-Regular.ttf"
import IBMPlexSans_BoldItalic from "./IBMPlexSans-BoldItalic.ttf"
import IBMPlexSans_Medium from "./IBMPlexSans-Medium.ttf"
import IBMPlexSans_Thin from "./IBMPlexSans-Thin.ttf"
import IBMPlexSans_SemiBold from "./IBMPlexSans-SemiBold.ttf"
import IBMPlexSans_Light from "./IBMPlexSans-Light.ttf"
import IBMPlexSans_ExtraLightItalic from "./IBMPlexSans-ExtraLightItalic.ttf"

const GlobalStyle = createGlobalStyle`
	@font-face {
		font-family: IBM Plex Sans Regular;
		src: url(${IBMPlexSans_Regular}) format('truetype')
	}
	@font-face {
		font-family: IBM Plex Sans Bold Italic;
		src: url(${IBMPlexSans_BoldItalic}) format('truetype')
	}
	@font-face {
		font-family: IBM Plex Sans Medium;
		src: url(${IBMPlexSans_Medium}) format('truetype')
	}
	@font-face {
		font-family: IBM Plex Sans Thin;
		src: url(${IBMPlexSans_Thin}) format('truetype')
	}
	@font-face {
			font-family: IBM Plex Sans Semibold;
			src: url(${IBMPlexSans_SemiBold}) format('truetype')
		}
	@font-face {
			font-family: IBM Plex Sans Light;
			src: url(${IBMPlexSans_Light}) format('truetype')
		}
	@font-face {
		font-family: IBM Plex Sans Extra-Light Italic;
		src: url(${IBMPlexSans_ExtraLightItalic}) format('truetype')
	}
`
export default GlobalStyle