import React from 'react'
import styled from "styled-components"
import { motion } from "framer-motion"
import {
	ArticleContainerCenter,
	ArticleTile,
	ArticleTileLeft,
	ArticleTileRight,
	ArticleTileOuter,
	ArticleTileInner,
	ArticleContainerTop,
	ArticleContainerTopLeft,
	ArticleContainerTopRight,
	ArticleTileHeader,
	ArticleTileMainliner,
	ArticleTileDescription,
	GiphyContainer
} from "./ArticleElements"
import BackButton from "../../components/BackButton"
import FontProvider from "../../fonts"
import { projectList, ProjectData } from '../../dataLists'

const Projects = () => {
	const projects = projectList.map((p: ProjectData) => {
		return (
			<ArticleTileOuter href="#">
				<ArticleTile>
					<ArticleTileLeft>
						<ArticleTileHeader>{p.title}</ArticleTileHeader>
						<ArticleTileMainliner>{p.subtitle}</ArticleTileMainliner>
						<ArticleTileDescription>{p.description}</ArticleTileDescription>
					</ArticleTileLeft>
					<ArticleTileRight>
						<GiphyContainer>
							<iframe src={p.img} width="100%" height="100%" frameBorder="0" className="giphy-embed" allowFullScreen></iframe><p><a href="https://giphy.com/gifs/8FDhJzbSLQvhmDVeTW"></a></p>							
						</GiphyContainer>
					</ArticleTileRight>
				</ArticleTile>
			</ArticleTileOuter>
		)
	})

	return (
		<Container>
			<FontProvider />
			<ArticleContainerTop>
				<ArticleContainerTopLeft>
					<BackButton direction="left" />
				</ArticleContainerTopLeft>
				<ArticleContainerTopRight>
					Projects
				</ArticleContainerTopRight>
			</ArticleContainerTop>
			<ArticleContainerCenter>
				{projects}
			</ArticleContainerCenter>
		</Container>
	)
}

const ContainerAnimation = {
	in: {
		x: 0,
		transition: {
			duration: 1
		}
	},
	out: {
		x: '100vw',
		transition: {
			duration: 1
		}
	}
}

export const Container = styled(motion.div).attrs(() => ({
	initial: "out",
	animate: "in",
	exit: "out",
	variants: ContainerAnimation,
}))`
	width:100%;
	height:100%;
	display:grid;
	grid-template-rows:20% auto;
	background-color:black;
	color:white;
	overflow:auto;
`

export default Projects
