import styled from "styled-components"
import { motion } from "framer-motion"
import {
	Link as RouterLink
} from "react-router-dom"
import BackArrow from "../images/BackArrow.svg"

const Button = styled(motion.button).attrs(() => ({
	whileHover: {
		scale: 1.1
	},
	whileTap: {
		scale: 0.9
	}
}))`
	background-color:white;
	border-radius:80px;
	border:none;
	background-color: white;
	width:100%;
	height:100%;
`

const Link = styled(RouterLink)`
	width:80px;
	height:80px;
	border-radius:80px;
`

interface Props {
	direction: 'left' | 'right' | 'up' | 'down'
}
const BackButton = ({ direction }: Props) => {
	const style = direction === 'left' ? {
		transform: 'rotate(-180deg)',
		width:'50%',
		height:'50%',
	} : direction === "right" ? {
		width:'50%',
		height:'50%',
	} : direction === "up" ? {
		transform: 'rotate(-90deg)',
		width:'50%',
		height:'50%',
	} : {
		transform: 'rotate(90deg)',
		width:'50%',
		height:'50%',
	}

	return (
		<Link to="/website">
			<Button>
				<img src={BackArrow} style={style} />
			</Button>
		</Link>
	)
}

export default BackButton