import React, { useRef, useState, useEffect } from 'react';
import { Redirect } from "react-router-dom"

type Pos = {
	x: number,
	y: number,
}

const BOARD_CHAR_REPRESENTATION = [
	['x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x'],
	['x', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'x', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'x'],
	['x', 'o', 'x', 'x', 'o', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'o', 'x', 'x', 'o', 'x'],
	['x', 'o', 'x', 'x', 'o', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'o', 'x', 'x', 'o', 'x'],
	['x', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'x'],
	['x', 'o', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'o', 'x'],
	['x', 'o', 'o', 'o', 'o', 'x', 'o', 'o', 'x', 'o', 'o', 'x', 'o', 'o', 'o', 'o', 'x'],
	['x', 'x', 'x', 'x', 'o', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'o', 'x', 'x', 'x', 'x'],
	['x', 'x', 'x', 'x', 'o', 'x', 'o', 'o', 'o', 'o', 'o', 'x', 'o', 'x', 'x', 'x', 'x'],
	['about', 'o', 'o', 'o', 'o', 'o', 'o', 'x', 'x', 'x', 'o', 'o', 'o', 'o', 'o', 'o', 'projects'],
	['x', 'x', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'x', 'x'],
	['x', 'o', 'o', 'o', 'o', 'x', 'o', 'o', 'o', 'o', 'o', 'x', 'o', 'o', 'o', 'o', 'x'],
	['x', 'o', 'x', 'x', 'o', 'x', 'x', 'o', 'x', 'o', 'x', 'x', 'o', 'x', 'x', 'o', 'x'],
	['x', 'o', 'x', 'x', 'o', 'o', 'o', 'o', 'x', 'o', 'o', 'o', 'o', 'x', 'x', 'o', 'x'],
	['x', 'o', 'x', 'x', 'o', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'o', 'x', 'x', 'o', 'x'],
	['x', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'o', 'x'],
	['x', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'articles', 'x', 'x', 'x', 'x', 'x', 'x', 'x', 'x'],
]

const SVG_DIMENSION = 100;
const BLOCK_DIMENSION: number = SVG_DIMENSION / (BOARD_CHAR_REPRESENTATION.length + 1)

interface CC {
	x: string
	o: string
}

function createBoardSVG(board: Array<Array<string>>, playerPosition: Pos) {
	let y = 0
	let x = 0
	const character_color: CC = {
		x: 'darkblue',
		o: 'black',
	}
	let svg_rects = []
	for (const y in board) {
		for (const x in board[y]) {
			const color: string = (Number(x) === playerPosition.x && Number(y) === playerPosition.y) ?
				'white' : character_color[board[y][x] as keyof CC]
			svg_rects.push(
				<rect
					key={Number(y) * BOARD_CHAR_REPRESENTATION.length + x}
					x={Number(x) * BLOCK_DIMENSION}
					y={Number(y) * BLOCK_DIMENSION}
					width={BLOCK_DIMENSION}
					height={BLOCK_DIMENSION}
					fill={color}
					strokeWidth='0.5'
					stroke='black'
				></rect>)
		}
	}
	return svg_rects
}

const Board = () => {
	const [playerPosition, _setPlayerPosition] = useState<Pos>({ x: 8, y: 8 })
	const [boardSVGElement, setBoardSVGElement] = useState<Array<JSX.Element>>()
	const [redirect, setRedirect] = useState<string | null>(null)
	const playerPositionRef = useRef(playerPosition)

	const setPlayerPosition = (p: Pos) => {
		playerPositionRef.current = p
		_setPlayerPosition(p)
	}

	useEffect(() => {
		if (BOARD_CHAR_REPRESENTATION[playerPosition.y][playerPosition.x] === 'about') {
			setRedirect("/website/about")
		} else if (BOARD_CHAR_REPRESENTATION[playerPosition.y][playerPosition.x] === 'projects') {
			setRedirect('/website/projects')
		} else if (BOARD_CHAR_REPRESENTATION[playerPosition.y][playerPosition.x] === 'articles') {
			setRedirect('/website/articles')
		} else {
			setBoardSVGElement(createBoardSVG(BOARD_CHAR_REPRESENTATION, playerPosition as Pos))
			setRedirect(null)
		}
	}, [playerPosition])

	const handleKeyPress = (e: any) => {
		const x = playerPositionRef.current.x
		const y = playerPositionRef.current.y

		if (e.key === 'ArrowRight' 
			&& BOARD_CHAR_REPRESENTATION[y][x + 1] !== 'x') {
			setPlayerPosition({
				y: y,
				x: x + 1
			})
		}
		if (e.key === 'ArrowLeft'
			&& BOARD_CHAR_REPRESENTATION[y][x - 1] !== 'x') {
			setPlayerPosition({
				y: y,
				x: x - 1
			})
		}
		if (e.key === 'ArrowDown'
			&& BOARD_CHAR_REPRESENTATION[y + 1][x] !== 'x') {
			setPlayerPosition({
				y: y + 1,
				x: x
			})
		}
		if (e.key === 'ArrowUp'
			&& BOARD_CHAR_REPRESENTATION[y - 1][x] !== 'x') {
			setPlayerPosition({
				y: y - 1,
				x: x
			})
		}
	}

	useEffect(() => {
		window.addEventListener('keydown', handleKeyPress)
		return () => window.removeEventListener('keydown', handleKeyPress)
	}, [])

	if (redirect) {
		return (<Redirect to={redirect} />)
	} else {
		return (
			<svg style={{ width: '100%', height: '100%' }} viewBox='0 0 100 100'>
				{boardSVGElement}
			</svg>
		)
	}
}

export default Board